.container {

    grid-template-rows: calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6) calc(100vh / 6);

}

a {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-size: 6vw;
    text-decoration: none;
    color: rgb(211, 211, 211);
    text-shadow: 3px 3px #2b2b2b;
}

@media (max-width: 1024px) {
    .container {
        grid-template-rows: calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7) calc(100vh / 7);
    }

    a {
        font-size: 11vw;
        background-color: rgba(0, 0, 0, 0.5);
    }
}

@keyframes fadeIn {
    from { filter: brightness(25%);}
    to { filter: brightness(50%);}
}

@keyframes fadeOut {
    from { filter: brightness(25%);}
    to { filter: brightness(50%);}
}

@keyframes fadeOutMain {
    from { filter: brightness(25%);}
    to { filter: brightness(50%);}
}

@media (min-width: 1024px) {

    a:hover {
        background-color: rgba(0, 0, 0, 0.5);
    }

    #backImg {
        animation-name: fadeOutMain;
        animation-duration: 1s;
    }

    #home:hover ~ #backImg {
        animation-name: fadeIn;
        animation-duration: 1s;
        content: url("http://placekitten.com/g/2000/1000");
    }

    #about:hover ~ #backImg {
        animation-name: fadeOut;
        animation-duration: 1s;
        content: url("http://placekitten.com/g/1900/1000");
    }

    #pano:hover ~ #backImg {
        animation-name: fadeIn;
        animation-duration: 1s;
        content: url("./static/img/pano.gif");
    }

    #desktop:hover ~ #backImg {
        animation-name: fadeOut;
        animation-duration: 1s;
        content: url("http://placekitten.com/g/1970/1000");
    }

    #stories:hover ~ #backImg {
        animation-name: fadeIn;
        animation-duration: 1s;
        content: url("https://media.giphy.com/media/3og0IGYBSB8WykDt16/giphy.gif");
    }

    #contact:hover ~ #backImg {
        animation-name: fadeOut;
        animation-duration: 1s;
        content: url("http://placekitten.com/g/1990/1000");
    }

}

